import Vue from 'vue'

Vue.prototype.$baseToast = function (args) {
    const message = args.message
    const title = args.title || '软件孵化实验室'
    const delay = args.delay || 2000
    const append = args.append || true
    const variant = args.variant || 'info'
    const toaster = args.toaster || 'b-toaster-top-center'
    this.$bvToast.toast(message, {
        title: title,
        autoHideDelay: delay,
        appendToast: append,
        variant: variant,
        toaster: toaster
    })
}

Vue.prototype.$infoToast = function info_toast (message) {
    this.$baseToast({
        message: message
    })
}

Vue.prototype.$secondaryToast = function secondary_toast(message) {
    this.$baseToast({
        message: message,
        variant: 'secondary'
    })
}

Vue.prototype.$dangerToast = function danger_toast(message) {
    this.$baseToast({
        message: message,
        variant: 'danger'
    })
}

Vue.prototype.$warningToast = function warning_toast(message) {
    this.$baseToast({
        message: message,
        variant: 'warning'
    })
}

Vue.prototype.$successToast = function success_toast(message) {
    this.$baseToast({
        message: message,
        variant: 'success'
    })
}

Vue.prototype.$primaryToast = function primary_toast(message) {
    this.$baseToast({
        message: message,
        variant: 'primary'
    })
}

