import Vue from 'vue'
import VueRouter from 'vue-router'
import HomePage from "../views/SignUp/HomePage";
import AboutPage from "../views/SignUp/AboutPage";
import ContactPage from "../views/SignUp/ContactPage";
import NewPage from "../views/SignUp/NewPage";
import QueryPage from "../views/SignUp/QueryPage";

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'HomePage',
    component: HomePage
  },
  {
    path: '/about',
    name: 'AboutPage',
    component: AboutPage
  },
  {
    path: '/contact',
    name: 'ContactPage',
    component: ContactPage
  },
  {
    path: '/new',
    name: 'NewPage',
    component: NewPage
  },
  {
    path: '/query',
    name: 'QueryPage',
    component: QueryPage
  }
]

const router = new VueRouter({
  mode: 'history',
  base: '/news/',
  routes,
})

export default router
