<template>
    <div class="add mb-5">
        <h3 class="page-header text-center mb-3">查询结果</h3>
        <b-card class="container">
            <div>
                <div class="well">
                    <div class="form-group">
                        <label for="uid">学号</label>
                        <input id="uid" v-model="userId" type="text" class="form-control" placeholder="学号（必填）" />
                    </div>
                    <button class="btn btn-outline-primary" @click="submitQuery">提交查询</button>
                </div>
            </div>
        </b-card>
    </div>
</template>

<script>
    export default {
        name: "QueryPage",
        data() {
            return {
                userId: ''
            }
        },
        methods: {
            submitQuery: function() {
                if(this.userId === '') {
                    this.$message.error('必填项不能为空！')
                } else {
                    this.$http.get(
                        '/user/' + this.userId,
                    ).then((response) => {
                        if(response.data.code === 1) {
                            this.$message.success(response.data.message)
                        } else {
                            this.$message.error(response.data.message)
                        }
                    }).catch((error) => {
                        this.$message.error('查询出现错误, 请稍后再试！')
                        console.log(error)
                    })
                }
            }
        }
    }
</script>

<style scoped>
    .add {
        margin-top: 35px;
    }
</style>
