<template>
    <div class="add container" style="padding-top: 35px">
        <h3 class="page-header text-center mb-3" data-aos="fade-down">填写报名信息</h3>
        <b-card data-aos="fade-up">
            <div class="well">
                <div class="form-group">
                    <label for="uid">学号</label>
                    <input
                            id="uid"
                            v-model="newinfo.userId"
                            type="text"
                            class="form-control"
                            placeholder="学号（必填）"
                    />
                </div>
                <div class="form-group">
                    <label for="name">姓名</label>
                    <input
                            id="name"
                            v-model="newinfo.name"
                            type="text"
                            class="form-control"
                            placeholder="姓名（必填）"
                    />
                </div>
                <div class="form-group">
                    <label for="class">班级</label>
                    <input
                            id="class"
                            v-model="newinfo.class"
                            type="text"
                            class="form-control"
                            placeholder="班级（必填）"
                    />
                </div>
                <div class="form-group">
                    <label for="gender">性别</label>
                    <select id="gender" class="form-control" v-model="newinfo.gender">
                        <option value="男">男</option>
                        <option value="女">女</option>
                    </select>
                </div>
                <div class="form-group">
                    <label for="qq">QQ号</label>
                    <input
                            id="qq"
                            v-model="newinfo.qq"
                            type="tel"
                            class="form-control"
                            placeholder="QQ号（必填）"
                    />
                </div>
                <div class="form-group">
                    <label
                            for="profile"
                    >个人简历(简述个人技术栈、目标方向、项目经历等或其他体现自学能力的内容。有博客者可以贴出地址。竞赛获奖是加分项。)</label
                    >
                    <textarea
                            id="profile"
                            v-model="newinfo.profile"
                            class="form-control"
                            rows="10"
                            placeholder="请各位同学一定要认真填写，顺便填写一周内来实验室的时间。"
                    ></textarea>
                </div>
                <button class="btn btn-outline-primary" @click="submitNewInfo">提交信息</button>
            </div>
        </b-card>
    </div>
</template>

<script>
    export default {
        name: "NewPage",
        data() {
            return {
                newinfo: {
                    userId: '',
                    name: '',
                    class: '',
                    gender: '男',
                    qq: '',
                    profile: ''
                }
            }
        },
        methods: {
            // 提交信息
            submitNewInfo: function() {
                if(
                    this.newinfo.userId !== '' &&
                    this.newinfo.name !== '' &&
                    this.newinfo.class !== '' &&
                    this.newinfo.gender !== '' &&
                    this.newinfo.qq !== '' &&
                    this.newinfo.profile !== ''
                ) {
                    let newCustomer = {
                        uid: this.newinfo.userId,
                        name: this.newinfo.name,
                        gender: this.newinfo.gender,
                        qq: this.newinfo.qq,
                        className: this.newinfo.class,
                        profile: this.newinfo.profile
                    }
                    console.log(newCustomer)
                    // 发送Post请求，添加用户信息
                    this.$http.post(
                        '/user/post',
                        JSON.stringify(newCustomer)
                    ).then((response) => {
                        if(response.data.code === 1) {
                            this.$message.success(response.data.message)
                        } else {
                            this.$message.error(response.data.message)
                        }
                    }).catch((error) => {
                        this.$message.error('报名出现错误, 请稍后再试！')
                        console.log(error)
                    })
                } else {
                    this.$message.error('对不起, 必填选项不能为空, 请检查后重试！')
                }
            }
        }
    }
</script>

